import { Component } from "react";
import './App.css';

class Tag extends Component {
  constructor(props) {
    super(props)
    this.state = {
      text: props.text
    }
  }

  render() {
    let color = this.state.text === "Works" ? "blue" : ""
    return (
      <span className={"tag " + color}>{this.state.text}</span>
    )
  }
}


class Game extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: props.data,
      active: false
    }
  }

  toggleAccordion = () => {
    this.setState({ active: !this.state.active })
  }

  location_images = (location) => {
    switch (location.toLowerCase()) {
      case "steam":
        return "assets/steam.svg";
      case "epic games store":
        return "assets/egs.svg";
      default:
        return "assets/generic.svg";
    }
  }

  vendor_images = (vendor) => {
    switch (vendor.toLowerCase()) {
      case "nvidia":
        return "assets/nvidia.svg";
      case "amd":
        return "assets/amd.svg";
      default:
        return "assets/generic.svg"
    }
  }

  render() {
    console.log(this.state.data.data)
    let lastIndex = 0;
    const tests = this.state.data.tests.map((test) => {
      const issues = test.issues.map((issue) => (
        <span key={lastIndex++} className="tag gray">{issue}</span>
      ))

      return (
        <>
          <div class="test_gpu">
            <img src={this.vendor_images(test.gpu_vendor)} alt={test.gpu_vendor} />
            <h4>{test.gpu_vendor} {test.gpu_info}</h4>
          </div>
          {test.description ? <p>{test.description}</p> : ""}
          <p>Fullscreen:  <Tag text={test.fullscreen} /></p>
          <p>Windowed:  <Tag text={test.windowed} /></p>
          {test.issues.length ? <p>Issues:  {issues}</p> : ""}
        </>
      )
    });

    return (
      <div className={"game " + (this.state.active ? "" : "hide_info")}>
        <div className="game_select" onClick={() => this.toggleAccordion()}>
          <div className="game_name">
            <img src={this.location_images(this.state.data.location)} alt={this.state.data.location} />
            <p>{this.state.data.name}</p>
          </div>
          <div className="game_status">
            {this.state.data.tweak ? <Tag text="Needs tweaks" /> : ""}
            <Tag text={this.state.data.status} />
          </div>
        </div>
        <div className="game_info">
          {this.state.data.tweak ? <><p className="spaced">{this.state.data.tweak}</p></> : ""}
          {tests}
        </div>
      </div>
    )
  };
}

class App extends Component {
  constructor() {
    super();
    this.state = {
      data: [],
      query: ""
    };
  }

  componentDidMount() {
    fetch("games.json").then((response) => response.json()).then((response) => {
      let lastIndex = 0
      const games = response.map(game => {
        game.status = "Works";
        game.tests.forEach((test) => {
          if (test.fullscreen !== "Works" || test.windowed !== "Works" || test.issues.length) {
            game.status = "Issues";
            return;
          }
        });
        game.index_id = lastIndex++;
        return game;
      })

      let urlParams = new URLSearchParams(window.location.search)

      this.setState({ data: games, query: (urlParams.has("search") ? urlParams.get("search") : "") })


    })
  }

  setQuery = (value) => {
    this.setState({ query: value })
  }

  render() {
    let filtered_data = this.state.data
    filtered_data = filtered_data.sort((a, b) => {
      return ((a.name.toLowerCase() < b.name.toLowerCase()) ? -1 : 1)
    }).filter(eachItem => {
      return (
        eachItem.location.toLowerCase().includes(this.state.query.toLowerCase()) ||
        eachItem.name.toLowerCase().includes(this.state.query.toLowerCase()) ||
        eachItem.status.toLowerCase().includes(this.state.query.toLowerCase()) ||
        (eachItem.tweak ? "needs tweaks".includes(this.state.query.toLowerCase()) : false)
      );
    });

    const gamesList = filtered_data.map((game_data) => (
      <Game key={game_data.index_id} data={game_data} />));

    return (
      <div className="App">
        <header>
          <h1>Arcade Compatibility</h1>
          <p>
            These are tests done by the community to check whether a game works or not on Arcade.
            Note that compatibility will vary depending on your hardware, and it's possible that
            you won't be able to host a game that is said to work here.
          </p>
          <input type="text" onChange={(e) => this.setQuery(e.target.value)} placeholder="Search for a game, status or store..." value={this.state.query} />
        </header>
        <main>
          {gamesList}
        </main>
      </div>
    );
  }
}

export default App;
